<template>
  <va-card :title="$t('tables.labelRestriction')" class="w-100">
    <div class="row mr-0">
      <div class="flex xs12 sm4">
        <va-date-picker
          :label="$t('forms.dateTimePicker.from')"
          v-model="search.from"
        />
      </div>
      <div class="flex xs12 sm4">
        <va-date-picker
          :label="$t('forms.dateTimePicker.to')"
          v-model="search.to"
        />
      </div>
      <va-button
        icon="fa fa-trash"
        flat
        slot="actions"
        color="danger"
        class="mr-0"
        @click="deletesearch"
      />
      <va-button
        icon="fa fa-search"
        flat
        slot="actions"
        color="success"
        class="mr-0"
        @click="searchDate"
      />
    </div>
    <va-data-table
      :fields="fields"
      :data="dataTable"
      :loading="loading"
      :totalPages="totalPages"
      @page-selected="readItems"
      api-mode
      hoverable
    >
      <template slot="marker" slot-scope="props">
        {{props.rowIndex + 1}}
      </template>
      <template slot="date" slot-scope="props">
        {{ formatDate(props.rowData.date)}}
      </template>
      <template slot="name_Restrictions_Type" slot-scope="props">
        {{ props.rowData.restrictions_Type.name}}
      </template>
      <template slot="name_Restrictions_State" slot-scope="props">
        {{ props.rowData.restrictions_State.name}}
      </template>
      <template slot="actions" slot-scope="props">
        <va-button flat small color="info" @click="showEditModal(props.rowData)" class="ma-0">
          {{ $t('tables.view') }}
        </va-button>
      </template>
    </va-data-table>

    <va-modal v-model="editModal"
      size="large"
      :title=" $t('generic.restrictionsDetailes') "
      hide-default-actions >
      <div class="row">
        <div class="flex xs12 md12">
          <va-card>
            <div class="row row-separated">
              <div class="flex xs3">
                <p class="display-2 mb-1 text--center" :style="{color: this.$themes.info}">{{ this.currentobject.id}}</p>
                <p class="text--center mb-1">{{ $t('tables.headings.restrictionNumber')}}</p>
              </div>
              <div class="flex xs3">
                <p class="display-2 mb-1 text--center" :style="{color: this.$themes.info}">{{formatDate( this.currentobject.date , false)}}</p>
                <p class="text--center no-wrap mb-1">{{$t('tables.headings.date') }}</p>
              </div>
              <div class="flex xs3">
                <p class="display-2 mb-1 text--center" :style="{color: this.$themes.info}">{{ this.currentobject.name_Restrictions_Type}}</p>
                <p class="text--center mb-1">{{ $t('tables.headings.name_Restrictions_Type') }}</p>
              </div>
              <div class="flex xs3">
                <p class="display-2 mb-1 text--center" :style="{color: this.$themes.info}">{{ this.currentobject.name_Restrictions_State}}</p>
                <p class="text--center mb-1">{{ $t('tables.headings.name_Restrictions_State')}}</p>
              </div>
            </div>
          </va-card>
        </div>
      </div>
      <div class="row">
        <va-data-table style="width: 100%;"
          :fields="restrictions_Sub_fields"
          :data="currentobject.restrictions_Sub"
          api-mode
          no-pagination
        >
          <template slot="marker" slot-scope="props">
            {{props.rowIndex + 1}}
          </template>
        </va-data-table>

      </div>
      <div class=" text--center">
        <p class="display-2 mb-1 text--center" :style="this.currentobject.totalDeabit==this.currentobject.totalCridt ? {color: this.$themes.success} :{color: this.$themes.danger}">{{ this.currentobject.totalDeabit==this.currentobject.totalCridt ? $t('modal.balancedRestrictions') :$t('modal.unbalancedRestrictions') }}</p>

      </div>
    </va-modal>
  </va-card>

</template>

<script>
import RestrictionService from '../../../../services/finances/RestrictionService'
import helper from '../../../../helper/helper'
export default {
  data () {
    return {
      search: {},
      index: 0,
      perPage: 10,
      totalPages: 0,
      dataTable: [],
      editModal: false,
      loading: true,
      currentobject: {},
    }
  },

  computed: {
    fields () {
      return [{
        name: '__slot:marker',
        width: '30px',
        dataClass: 'text-center',
        title: '#',
      }, {
        name: 'id',
        dataClass: 'text-center',
        title: this.$t('tables.headings.restrictionNumber'),
      }, {
        name: '__slot:date',
        title: this.$t('tables.headings.date'),
      },
      {
        name: '__slot:name_Restrictions_Type',
        title: this.$t('tables.headings.name_Restrictions_Type'),
      },

      {
        name: '__slot:name_Restrictions_State',
        title: this.$t('tables.headings.name_Restrictions_State'),
      },
      {
        name: 'notes',
        title: this.$t('tables.headings.notes'),
      },
      {
        name: '__slot:actions',
        dataClass: 'text-right',
      }]
    },
    restrictions_Sub_fields () {
      return [{
        name: '__slot:marker',
        width: '30px',
        dataClass: 'text-center',
        title: '#',
      }, {
        name: 'dept',
        dataClass: 'text-center',
        title: this.$t('tables.headings.dept'),
      }, {
        name: 'credit',
        title: this.$t('tables.headings.credit'),
      },
      {
        name: 'code_Accounts',
        title: this.$t('tables.headings.code_Accounts'),
      },

      {
        name: 'name_Accounts',
        title: this.$t('tables.headings.name_Accounts'),
      }, {
        name: 'notes',
        title: this.$t('tables.headings.notes'),
      }]
    },
  },
  created () {
    this.readItems()
  },
  methods: {
    riquierdvalidation (data) {
      return helper.riquierdvalidation(data)
    },
    numircvalidation (data) {
      return helper.numircvalidation(data)
    },
    numircriquierdvalidation (d) {
      return helper.numircriquierdvalidation(d)
    },

    /*  api Padination */
    readItems (page = 1) {
      this.loading = true
      console.log('fkghg')
      const params = {
        PageSize: this.perPage,
        PageNumber: page,
      }
      RestrictionService.getAllPagnation(params)
        .then(response => {
          this.dataTable = response.data
          var pagination = JSON.parse(response.headers['x-pagination'])
          this.totalPages = pagination.TotalPages
          this.loading = false
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        })
    },
    searchItems (page = 1) {
      this.loading = true
      console.log('fkghg')
      const params = {
        PageSize: this.perPage,
        PageNumber: page,
        dateFrom: this.search.from,
        dateTo: this.search.to,
      }
      RestrictionService.SearchByDates(params)
        .then(response => {
          this.dataTable = response.data
          var pagination = JSON.parse(response.headers['x-pagination'])
          this.totalPages = pagination.TotalPages
          this.loading = false
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        })
    },
    /* end api Padination */

    formatDate (dateString, FullDate = true) {
      if (FullDate) {
        const options = { weekday: 'short', year: 'numeric', month: 'numeric', day: 'numeric', hour: 'numeric', minute: 'numeric' }
        return new Date(dateString).toLocaleDateString('en-GB', options)
      } else {
        const options = { year: 'numeric', month: 'numeric', day: 'numeric' }
        return new Date(dateString).toLocaleDateString('en-GB', options)
      }
    },
    searchDate () {
      this.searchItems(1)
    },

    deletesearch () {
      this.search = {}
      this.readItems()
    },
    /* edit model */
    showEditModal (cuobject) {
      this.editModal = true
      this.currentobject = cuobject
      var totalCridt
      for (let index = 0; index < cuobject.restrictions_Sub.length; index++) {
        totalCridt += cuobject.restrictions_Sub[index].credit
      }
      this.currentobject.totalCridt = totalCridt
      var totalDeabit
      for (let index = 0; index < cuobject.restrictions_Sub.length; index++) {
        totalDeabit += cuobject.restrictions_Sub[index].dept
      }
      this.currentobject.totalDeabit = totalDeabit
    },
    /* end edit model */
  },
}

</script>

<style lang="scss">
  i.fa.fa-exclamation {
    color: red !important;
  }

</style>
