import http from '../../http-common'

class RestrictionService {
  getAll () {
    return http.get('Restrictions')
  }

  getAllPagnation (params) {
    return http.get('Restrictions', { params })
  }

  SearchByDates (params) {
    return http.get('SearchByDates', { params })
  }

  get (id) {
    return http.get(`Restrictions?Id=${id}`)
  }

  create (data) {
    return http.post('Restrictions', data)
  }

  update (id, data) {
    return http.put(`Restrictions?Id=${id}`, data)
  }

  delete (id) {
    return http.delete(`Restrictions?Id=${id}`)
  }
}

export default new RestrictionService()
